.detentions {
  .detention {
    margin: 1em 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1em;

    &__progress {
      grid-column: 2 / 4;
      position: relative;
      width: 50vw;
      background-color: darkgrey;
      height: 1em;
      border-radius: 0.5em;

      &--value {
        border-radius: 0.5em 0 0 0.5em;
        height: 100%;
      }
      &--desc {
        position: absolute;
        width: 100%;
        text-align: center;
        top: 0;
        color: white;
      }
    }

    &__statistic {
      text-align: right;
      &.bad {
        color: red;
      }
    }
  }
}